import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { getCategories } from 'apiServices'
import Img from '../assets/icons/sayur-box-logo.svg'
import { withRouter } from 'react-router';

const LandingPage = () => {
    const dispatch = useDispatch()
    const categoryList = useSelector(state => state.categoryList)
    useEffect(() => {
        if (!categoryList?.length) getCategories({ dispatch })

    }, []);
    return (
        <div className='h-screen d-flex w-100 align-items-center d-flex justify-content-center'>
            <img className='w-25' src={Img} alt='Sayurbox-icon' />
        </div>
    )
}

export default withRouter(LandingPage)